@import "../../style.scss";

.stories {
  display: flex;
  gap: 10px;
  height: 250px;
  margin-bottom: 30px;
  
  border-radius:11px solid red;;
  @include mobile {
    height: 50px;
    margin-bottom: 0;
    justify-content: space-between;
  }
  
  @include tablet{
    height: 150px;
    gap: 20px;
  }
  #videobox{
    height:250px;
    width: 100%;
    position: relative;
    border-radius: 1px solid red;
    display: none;
  }
  .story {
    flex: 1;
    border-radius: 10px;
    overflow: hidden;
    position: relative;
    // display: none;
    

    @include mobile {
      flex: none;
      width: 50px;
      height: 50px;
      border-radius: 50%;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }


    span {
      position: absolute;
      bottom: 10px;
      left: 10px;
      color: white;
      font-weight: 500;
      @include mobile {
        display: none;
      }
    }

    button {
      position: absolute;
      bottom: 40px;
      left: 10px;
      color: white;
      background-color: #5271ff;
      border: none;
      border-radius: 50%;
      width: 30px;
      height: 30px;
      cursor: pointer;
      font-size: 30px;
      display: flex;
      align-items: center;
      justify-content: center;

      @include mobile {
       left: 0;
       right: 0;
       top: 0;
       bottom: 0;
       margin: auto;
      }
    }
  }
}