@import "../../style.scss";
.rightBar {
    @include themify($themes){
    flex: 3;
    position: sticky;
    top: 70px;
    height: calc(100vh - 70px);
    overflow: scroll;
    background-color: themed("bgSoft");

    @include mobile{
        display: none;
      }
      @include tablet{
        display: none;
      }

    &::-webkit-scrollbar {
        display: none;
    }

    .container {
        padding: 20px;

        .item {
            -webkit-box-shadow: 0px 0px 15px 1px rgba(0, 0, 0, 0.09);
        -moz-box-shadow: 0px 0px 15px 1px rgba(0, 0, 0, 0.09);
        box-shadow: 0px 0px 15px 1px rgba(0, 0, 0, 0.09);
        padding: 20px;
        margin-bottom: 20px;
        background-color: themed("bg");
        border-radius: 10px;

            span {

                color: gray;
            }


            .user {
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin: 8px 0px;


                .userInfo {
                    display: flex;
                    align-items: center;
                    gap: 20px;
                    position: relative;

                    img{
                        height: 40px;
                        width: 40px;
                        border-radius: 50%;
                        object-fit: cover;
                    }

                    .online{
                        width: 12px;
                        height: 12px;
                        border-radius: 50%;
                        background-color: limegreen;
                        position: absolute;
                        top: 0;
                        left: 30px;
                    }

                    p{
                        color: themed("textColorSoft");

                    }
                    span{
                        font-weight: 500;
                        color: themed("textColor");
                    }
                }


                .buttons {
                    display: flex;
                    align-items: center;
                    gap: 10px;


                    button{
                        border: none;
                        padding: 5px;
                        color: white;
                        cursor: pointer;

                        &:first-child{
                            background-color: royalblue;
                            border-radius: 15px;
                        }
                        &:last-child{
                            background-color: red;
                            border-radius: 15px;
                        }

                    }
                }
            }
        }

        
    }

}
}